import './startscreen.css';

const StartScreen = ({startGame}) => {
    return (
        <div className="StartScreen">
            <h1>Secret Word</h1>
            <p>Clique no boto abaixo para comear o jogo</p>
            <button onClick={startGame}>Comear o jogo</button>
        </div>
    )
}

export default StartScreen;